<template lang="en">
    <section>
      
        <!-- <div> -->
            <!-- <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('AddApprovalChain')}}</span>
                    </div>
                </div>
            </div>
        </div> -->
        <!-- <div class="border p-3"> -->
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form> 
            <small class="text-danger">{{ $t('MandatoryString') }}</small>
        <!-- </div> -->
    </section>
    </template>

<script>
import DataService from '../../services/DataService';
import Manage from './Manage.vue';
import moment from "moment"
export default {

    data() {

        return {
            isLoading: false,
            DataLoaded: 0,
            ApprovalChainId: 0,
            groupList: [],
            TagsSelectedArray: [],
            location: [],
            Userlist: [],
            FormSchema: [{
                layoutType: "double",
                Data: [
                    {
                        astype: "DateField",
                        label: this.$t('Date'),
                        name: "ImplementDate",
                        value: "",
                        placeholder: "",
                        validationRules: { "required": true },
                    },

                    {
                        astype: "SelectField",
                        label: this.$t('Location'),
                        name: "Location",
                        value: 1001,
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                            onChange: this.GetUsers
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: 0,
                        placeholder: "",
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [{ value: 1, name: "User" }, { value: 0, name: "Company Holidays" }],

                        }
                    },
                    {
                        astype: "MultiSelectField",
                        label: this.$t('Users'),
                        name: "Users",
                        value: "",
                        mode: "tag",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: []
                        },
                    },
                    {
                        astype: "TextAreaField",
                        label: this.$t('Comments'),
                        name: "comment",
                        value: "",
                        placeholder: "",
                    },

                ]
            }

            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
            },

            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.onCancel
            }
            ],
        }
    },
    created: async function () {
        var vm=this
        vm.GetCompanyLocation();
        console.log("vm.$route.params.id",vm.$route.params.id)
        if (!vm.isBlank(vm.$route.params.id) && vm.$route.params.id != "0") {
            let id = decodeURIComponent(vm.$route.params.id);
            await vm.Manage(id);
        }
        else {
            if (vm.$route.params.id == "0") {
                vm.Manage(0);

            }
            else {
                vm.$router.push({ name: "add", params: { id: 0 } });
                vm.Manage(0);
            }
        }

    },
    methods: {
        GetCompanyLocation: async function () {
            await DataService.GetCompanyLocation().then((response) => {
                this.location = response.data

                if (this.location != null) {
                    this.location.map((item) => {
                        this.FormSchema[0].Data[1].config.options.push({
                            name: item.LOCATION_NAME,
                            value: item.COMPANY_LOCATION_ID
                        })
                    })

                }
            })
        },
        GetUsers: async function () {
            var locationId = this.FormSchema[0].Data[1].value
            var params = `locationId=${locationId}&moduleName=WORKDAY`
            await DataService.GetUserListByLocationId(params).then(response => {
                this.Userlist = response.data
                if (this.Userlist != null) {
                    this.Userlist.map((item) => {
                        this.FormSchema[0].Data[3].config.options.push({
                            name: item.userName,
                            value: item.userId
                        })
                    })
                }
            })
        },
        Manage:async function(id){
            var vm = this;
            var param=`id=${id}`
            await DataService.WorkingDay(param).then(async response => {
                vm.FormSchema[0].Data[0].value = response.data.workDate;
                vm.FormSchema[0].Data[1].value = response.data.companyLocationId;
                await vm.GetUsers();
                var getUserlocation = [];
                var getmulti = response.data.users.split(',');
                vm.FormSchema[0].Data[4].value = response.data.comment;
                getmulti.forEach(x=>{
                    var checkExist = vm.FormSchema[0].Data[3].config.options.find(z=> z.value == x);
                    if(checkExist != undefined){
                        getUserlocation.push({label:checkExist.name,value:checkExist.value});
                    }
                })
                vm.FormSchema[0].Data[3].value = getUserlocation;
            })
        },
        onSubmit: async  function (formfields) {
            var vm = this;
            var ids = '';
            formfields.Users.forEach(item => {
                ids += item.value + ','
            });
            var obj = {                                
                CompanyLocationId: formfields.Location,                   
                WorkDate: moment(new Date(formfields.ImplementDate), 'DD/MM/YYYY').format('YYYY-MM-DD'),
                IsWorking: formfields.Status,
                Comment: formfields.comment,                 
                Users: ids                  
            }
            //var a= JSON.stringify(obj);
            await DataService.Manage(obj).then(response => {
          
            })
          
        },
        onCancel:async function(){
            this.$router.push({ name: "WorkingDay"})
        }

    }
}
</script>